<template>
  <div
    class="d-flex align-items-center flex-column py-5 border-top border-success"
    style="border-top-width: 3px !important"
  >
    <div>
      <h2 class="mb-5 coloured-underline">Recent Articles</h2>
    </div>
    <b-container fluid>
      <b-row>
        <b-col v-for="article in data" :key="article._path" class="mb-3" cols="12" md="4">
          <ContentRenderer :value="article">
            <b-card
              :title="article.title"
              :img-alt="article.title || 'https://place-hold.it/1200x600/#28a74/#21252&text=GoTrash'"
              :img-src="article.img"
              img-top
              tag="article"
            >
              <BCardText>
                {{ article.description }}
              </BCardText>
              <b-button :to="article._path" variant="success">Read More</b-button>
            </b-card>
          </ContentRenderer>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script setup>
  const date = new Date();
  const formatter = date =>
    date.toLocaleString("en-US", {
      minimumIntegerDigits: 2,
      useGrouping: false
    });
  const { data } = await useAsyncData("recent-articles", () =>
    queryContent("blog")
      .where({
        date: { $exists: true, $lte: new Date() },
        draft: { $not: true },
        hidden: { $not: true }
      })
      .sort({ date: 1 })
      .limit(3)
      .find()
  );
</script>
